import { GraphQLClient } from "graphql-request";

export const QueryCustomAPI = () => {
  const endpoint = 'https://us1.prisma.sh/public-gingergrabber-650/hackernews-node/dev';

  const client = new GraphQLClient(endpoint, { headers: { "Authorization": "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjp7InNlcnZpY2UiOiJoYWNrZXJuZXdzLW5vZGVAZGV2Iiwicm9sZXMiOlsiYWRtaW4iXX0sImlhdCI6MTUyODQ5NjgxNywiZXhwIjoxNTI5MTAxNjE3fQ.iFlxquLzkyu9R1AjBA3V2-QKAM9BQ1z6gALGUPxzu7M" } });

  const query = `query {
    links {
      url,
      id,
      createdAt,
      description,
      votes{id}
    },
    users {
      name,
      email
    }
  }`

  client.request(query).then(data => {
    createHTML(data);
  });
}

const createHTML = (data) => {

  
  console.log(data);
  
  
  for (let j = 0; j < data.links.length; j++) {
    const elUserData = document.querySelector('#userData');
    let aTag = document.createElement('a');
    let pTag = document.createElement('p');
    let descriptionTag = document.createElement('p');
    let iTag = document.createElement('i');
    let divTag = document.createElement('div');
    let secHead = document.createElement('section');
    let secBody = document.createElement('section');
    let secDescription = document.createElement('section');
    let smallTag = document.createElement('small');
    pTag.innerText = data.users[1].name;
    pTag.className = 'link__user';
    secHead.appendChild(pTag);
  
      iTag.innerText = 'No rating';
      if (data.links[j].votes.length > 0 || j === data.links.length) {
        iTag.innerText = '';
        iTag.className = 'em em-unicorn_face';
      }
    
      aTag.href = data.links[j].url;
      aTag.innerText = data.links[j].url;
    
      let postDate = new Date(data.links[j].createdAt)
      let month = postDate.getMonth();
      let year = postDate.getFullYear();
      let date = postDate.getDate();
      let dateHtml = `<div>${month}/${date}/${year}</div>`
      smallTag.innerHTML = dateHtml;
    
      descriptionTag.innerText = data.links[j].description;
  
      secHead.appendChild(smallTag);
      secBody.appendChild(aTag);
      secBody.appendChild(iTag);
      secDescription.appendChild(descriptionTag);
    
      divTag.className = 'link__wrapper'
    
      divTag.appendChild(secHead);
      divTag.appendChild(secBody);
      divTag.appendChild(secDescription);
      elUserData.appendChild(divTag);
    }
  




}

export default QueryCustomAPI()


